.profileCard {
    transition-duration: .3s;
    color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-image: linear-gradient(45deg, rgba(0, 162, 255, 0.3), rgba(255, 255, 255, 0.3) 20%, rgba(0, 162, 255, 0.3));
    border-radius: 10% 0;
    padding: 2vh;
    height: 40vh;
    width: 15vw;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
}

.profileCard:hover {
    cursor: pointer;
    background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.3), rgba(0, 162, 255, 0.3) 20%, rgba(255, 255, 255, 0.3));
    border-radius: 0 0 10% 10%;
}

.profileCard:hover .profilePic {
    border-radius: 3px;
}

.profilePic {
    transition-duration: .5s;
    border: .4vh solid rgb(128, 0, 128);
    border-radius: 50%;
    width: 100%;
}

.people {
    font-size: 4vh;
}

.job {
    font-size: 2vh;
}

@media (width < 720px) {
    .profileCard {
        width: 350px;
        height: 400px;
    }

    .profilePic {
        width: 300px;
    }
}