.info {
    color: white;
    background-image: linear-gradient(to right, transparent, rgba(0, 162, 255, 0.3), transparent);
    border-radius: 10%;
    padding: 3vw;
    margin: 0 10vw;
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    box-shadow: 0px 0px 40px rgba(0, 162, 255, 0.5);
}

.info > p {
    font-size: 3vh;
}

.info > h1 {
    font-size: 5vh;
}


.bigLogo {
    max-height: 400px;
    max-width: 400px;
    margin: 20px 0;
    mix-blend-mode: exclusion;
}

.founders {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    gap: 2vw;
    margin: 3vh 2vw;
}

@media (width < 720px) { 
    .founders {
        flex-direction: column;
    }
    
    .info {
        margin: 0;
        border-radius: 12px;
    }
}