* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scrollbar-width: thin;
  scrollbar-color: transparent rgba(0, 0, 239, 0.526);
}

*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: rgba(95, 156, 247, 0.108);
  border-radius: 20px;
}

.body {
  background-color: #0c1633;
  background-image: linear-gradient(rgb(18, 0, 78) 15%, #0c1633 85%);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

header {
  background-image: linear-gradient(to bottom, rgb(18, 0, 78) 90%, rgba(0, 0, 239, 0.526));
  width: 100%;
  padding: 1vh 0  2vh;
  margin-bottom: 1px;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
}

footer, header {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  font-size: 2.5vh;
}

main {
  background-image: linear-gradient(to right, transparent, rgba(95, 237, 247, 0.1), transparent);
  min-height: 70vh;
  width: 100%;
  border-radius: 10%;
  padding: 5vh 2vw;
}

footer{
  font-size: 2vh;;
  color: rgba(255, 255, 255, 0.377);
  background-image: linear-gradient(to top, transparent 70%, rgba(0, 0, 239, 0.526));
  width: 100%;
  height: 10vh;
}

@media (width < 720px) {
  header {
    top: 0;
    position: fixed;
    background-color: #0c1633;
    border-radius: 0;
    height: 10vh;
    justify-content: end;
  }

  .body {
    margin-top: 7vh;
  }
}