.card {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(45deg, rgba(128, 0, 128, 0.3), rgba(255, 255, 255, 0.3) 20%, rgba(128, 0, 128, 0.3));
    height: 45vh;
    min-width: 40vh;
    border-radius: 12px;
    text-align: center;
}

.card > h3 {
    font-size: 3vh;
}