.cardWheelContainer {
    background-image: linear-gradient(to right, transparent, rgba(0, 162, 255, 0.3), transparent);
    color: white;
    text-align: center;
    padding: 2vh;
    margin: 5vh 0;
}

.cardWheel {
    display: flex;
    flex-direction: row;
    padding: 2vh 3vw;
    justify-content: space-between;
    align-items: center;
    gap: 3vw;
    overflow-x: scroll;
}

.cardWheelContainer > h1 {
    font-size: 3vh;
}