.navigationMenu {
    display: flex;
    flex-direction: row;
    gap: 2vw;
    align-items: flex-end;
    justify-content: space-around;
}

.navItem {
    transition-duration: .3s;
    text-decoration: none;
    color: #7c65ff;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    padding: 0 1vw;
    padding-bottom: .5vh;
    border-radius: 30%;
    border-bottom: 1px solid transparent;
}


.navItem:hover {
    color: white;
}

.navItem:hover, #active{
    border-bottom: .6vw solid #11bdff;
}


.logo {
    height: 20vh;
}

@media (width < 720px) {
    .logo {
        display: block;
        height: 7vh;
    }

    .navItem {
        border-radius: 0;
    }
  }